import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'statusIrPipe'
})
export class StatusIrPipePipe implements PipeTransform {

    transform(status: string): any {
        switch (status) {
            case 'auto':
                return 'T ng';
            case 'on':
                return 'M';
            case 'off':
                return 'Tt';
            default:
                return 'Cha t';
        }
    }

}
